import Vue from 'vue'
import VueRouter from 'vue-router'
import Home from '../views/Home.vue'
import Peizhi from '../views/Peizhi.vue'
import Dingdan from '../views/Dingdan.vue'
import Shixiang from '../views/Shixiang.vue'
import Liulan from '../views/Liulan.vue'

Vue.use(VueRouter)

const routes = [
  {
    path: '*',
    name: 'Home',
    component: Home
  },
  // {
  //   path: '/',
  //   name: 'Home',
  //   component: Home
  // },
  {
    path: '/Peizhi',
    name: 'Peizhi',
    component: Peizhi
  },
  {
    path: '/Dingdan',
    name: 'Dingdan',
    component: Dingdan
  },
  {
    path: '/Shixiang',
    name: 'Shixiang',
    component: Shixiang
  },
  {
    path: '/Liulan',
    name: 'Liulan',
    component: Liulan
  },
]

const router = new VueRouter({
  routes
})

export default router
